
.deep-thoughts,.action-house,.password-generator,.weather-dashboard {
  box-shadow: 5px 5px 18px rgb(235, 225, 225);
    position: relative;
    width: 100%;
    max-width: 500px;

  }
  
  .project-bg {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .project-text {
    color:#de7e42;
    position: absolute;
    font-size: 2rem;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding-top: 50px;
    z-index: 900;
    opacity: 0;
    transition: all .5s ease-in-out;
  }
  .project-text:hover {
    background-color: rgba(255,255,255,.7);
    opacity: 1;
    border: 10px solid #de7e42;

  }
  
  .project-text h3 {
    justify-content: center;
    font-size: 3vh;
  }
  .project-text a {
    text-decoration: none;
    font-weight: bold;
    color:#282841;
    transition: all .3s ease-in-out;
  }
  
  .project-info a {
    text-decoration: none;
    font-weight: bold;
    color: #282841;
    transition: all .3s ease-in-out;
  }
  
  .project-text a:hover {
    color:#151618;
  }
  p {
    display: flex;
    font-size:2vh;
    line-height: 2rem;
    justify-content: right;
  }
  
  a {
    color: #fafafa;
    text-decoration: none;
    opacity: .5;
  }
  
  a:hover {
    opacity: 1;
    transition: all .2s ease-in-out;
  }
  .slide {
    opacity : 0;
    transition-duration:1s ease;
}
.slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform:scale(1.08); 
}
.icons{
  font-size: 2.2vh;
}
@media screen and (max-width:940px){

  .project-bg {
    border: 3px solid #f09927;
    

  }
  .project-text {
    border: 3px solid #f09927;
    top:70%;
    opacity: 1;
    padding: 0;
    background-color:rgba(14, 14, 14, 0.95) ;
    display: flex;
  }
  .project-text h3{
    margin: 0.25rem;
  }
  .project-text a {
    opacity: 1;
    color: #ffffff;
    font-size: 2vh;
    text-decoration: none;
    justify-content: left;

  }
  .url{
    padding: 2px;
  }
  .repo-desc{
    margin-top:auto ; 
    font-size: 1.5vh;

    
   }
  .project-text a:touch-action{
    text-decoration: underline;
  }
  p{
    padding-left: 4rem;
  }

}
